import SortView from '../view/sort-view.js';
import EventListView from '../view/event-list-view.js';
import FormEditView from '../view/form-edit-view.js';
import PointView from '../view/point-view.js';
import FormCreationView from '../view/form-creation-view.js';
import { render } from '../render.js';

export default class BoardPresenter {
  sortComponent = new SortView(); // (new)   
  eventListComponent = new EventListView();

  constructor({container, pointModel}) {
    this.container = container;
    this.pointModel = pointModel;
  }

  init() { //        
    this.eventPoints = [...this.pointModel.getPoints()]; //   ,    
    render(this.sortComponent, this.container); //    
    render(this.eventListComponent, this.container);

    render(new FormEditView({ //    :
      points: this.eventPoints[0], //     
      checkedOffers: this.pointModel.getOffersById(this.eventPoints[0].type, this.eventPoints[0].offers), //,    ,     
      offers: this.pointModel.getOffersByType(this.eventPoints[0].type), //     
      destinations: this.pointModel.getDestinationById(this.eventPoints[0].destination) //,    
    }), this.eventListComponent.getElement() //    
    );

    render(new FormCreationView(), this.eventListComponent.getElement());

    for (let i = 1; i < this.eventPoints.length; i++) { //  .    :
      render(new PointView({
        points: this.eventPoints[i], // 
        offers: this.pointModel.getOffersById(this.eventPoints[i].type, this.eventPoints[i].offers), //,    .
        destinations: this.pointModel.getDestinationById(this.eventPoints[i].destination)// ,    .
      }),
      this.eventListComponent.getElement()); //     
    }
  }
}
